import React, { FC } from 'react';
import { Button, Card } from 'react-bootstrap';
import { Col, Row } from 'react-bootstrap/esm';

import { TProduct } from '../../helpers/types';
import { getProductImgUrl } from '../../helpers/urls';
import './ProductCard.scss';

export interface IProductCard {
  product: TProduct;
  onBuyBtnClick: () => void;
}
export const ProductCard: FC<IProductCard> = ({ product, onBuyBtnClick }) => {
  const {
    currency,
    displayName,
    measure,
    pictureUrl,
    price,
  } = product;
  return (
    <Card bg="Primary" className="ProductCard text-center">
      <Card.Img variant="left" src={getProductImgUrl(pictureUrl)} />
      <Card.Header><h3>{displayName}</h3></Card.Header>
      <Card.Body>
        <Row>
          <Col className="col-8"><p className="h5 mt-1">{price} {currency} за {measure}</p></Col>
          <Col className="col-4"><Button onClick={onBuyBtnClick}>Купити</Button></Col>
        </Row>
      </Card.Body>
    </Card>
  );
};
