import React from "react";
import { Col, Container, Row } from 'react-bootstrap';

import './Header.scss';

export const Header = () => {
  return (
    <Container className="Header">
      <Row>
        <Col md={7}>
          <img src="./img/logo_header.png" className="img-fluid" alt="Товари для миловарів та кремоварів &ldquo;Лаванда&rdquo;" />
        </Col>
        <Col md={5}>
          <h4>
            Товари для миловарів та кремоварів
          </h4>
          <h2>
            𝐿𝒶𝒱𝒶𝓃𝒹𝒶 𝒟𝑒𝐿𝒾𝑔𝒽𝓉𝓈
          </h2>
        </Col>
      </Row>
    </Container>
  );
};

