import React, { FC } from 'react';
import { Container, Nav, Navbar } from 'react-bootstrap';

import { CartImg } from '../../images';
import './MainNavbar.scss';

export interface IMainNavbarItem {
  id: number;
  displayName: string;
}
export interface IMainNavbar {
  navItems: IMainNavbarItem[];
  isCartEmpty: boolean;
  onCartClick: () => void;
  onNavSelect: (id: string) => void;
}
export const MainNavbar: FC<IMainNavbar> = ({navItems, isCartEmpty, onCartClick, onNavSelect}) => {
  return (
    <div className="MainNavbar">
      <Navbar bg="light" expand="sm" fixed="top">
        <Container>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav
              variant="tabs"
              defaultActiveKey={navItems[0].id.toString()}
              onSelect={(selectedKey: any) => onNavSelect(selectedKey)}
            >
              {navItems.map((item: IMainNavbarItem) => {
                const { id, displayName } = item;
                const key = id.toString();
                return (
                  <Nav.Item key={key}>
                    <Nav.Link eventKey={key} href="#">{displayName}</Nav.Link>
                  </Nav.Item>
                );
              })}
            </Nav>
          </Navbar.Collapse>
          <Navbar.Brand className={`cart-container ${isCartEmpty ? '' : 'active'}`} onClick={onCartClick}>
            <CartImg />
          </Navbar.Brand>
        </Container>
      </Navbar>
    </div>
  );
};

