import React, { FC } from 'react';
import { Button, Col, Container, Row } from 'react-bootstrap';
import './ProductCounter.scss';


export interface IProductCounter {
  count: number;
  measureUnit: string;
  onIncreaseCount: () => void;
  onDecreaseCount: () => void;
}
export const ProductCounter: FC<IProductCounter> = ({
  count,
  measureUnit,
  onIncreaseCount,
  onDecreaseCount
}) => {
  return (
    <Container className="ProductCounter border-0">
      <Row>
        <Col align="center"><Button onClick={onDecreaseCount} disabled={count === 0}> - </Button></Col>
        <Col align="center"><h3>{count} {measureUnit}</h3></Col>
        <Col align="center"><Button onClick={onIncreaseCount}> + </Button></Col>
      </Row>
    </Container>
  );
};
