import React, { FC, useState } from 'react';
import { map } from 'lodash';
import { Button, Col, Container, Image, Modal, Row } from 'react-bootstrap';

import { TCart, TCartItem, TUser } from '../../helpers/types';
import './CartModal.scss';
import { UserDataForm } from '../UserDataForm/UserDataForm';

export interface ICartModal {
  cart: TCart;
  cartCost: string;
  isCartEmpty: boolean;
  isCartModalVisible: boolean;
  userData: TUser;
  onCloseModal: () => void;
  onPurchaseBtnClick: (newUserData: TUser) => void;
}
export const CartModal: FC<ICartModal> = ({
  cart,
  cartCost,
  isCartEmpty,
  isCartModalVisible,
  userData,
  onCloseModal,
  onPurchaseBtnClick,
}) => {
  const [isFormVisible, setIsFormVisible] = useState<boolean>(false);

  const onApproveCartBtnClick = () => {
    if (isCartEmpty) {
      onCloseModal();
      return;
    }

    setIsFormVisible(true);
  }

  const handleSendOrder = (newUserData: TUser) => {
    setIsFormVisible(false);
    onPurchaseBtnClick(newUserData);
  }

  const handleRejectBtnClick = () => {
    setIsFormVisible(false);
    onCloseModal();
  }
  
  return (
    <Modal
      className="CartModal"
      centered
      show={isCartModalVisible}
      size="xl"
      aria-labelledby="contained-modal-title-vcenter"
      onHide={onCloseModal}
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          Ваше замовлення
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Container>
          {isCartEmpty 
            ? <Row>
                <Col className="fw-semibold fs-3">Ваш коший порожній</Col>
              </Row>
            : isFormVisible ? null : [...map(cart, (cartItem: TCartItem) => {
              const {
                categoryId,
                productId,
                displayName,
                pictureUrl,
                displayCount,
                displayCost,
                displayPriceShort,
              } = cartItem;
                return (
                  <Row key={`c${categoryId}-p${productId}`} className="pt-2">
                    <Col><Image rounded src={pictureUrl} /></Col>
                    <Col>{displayName}</Col>
                    <Col className="text-end">{displayPriceShort}</Col>
                    <Col className="text-end">{displayCount}</Col>
                    <Col className="text-end">{displayCost}</Col>
                  </Row>
                );
              }),
              <Row key="total-row" className="fw-semibold fs-3 text-end">
                <Col>Разом:</Col>
                <Col>{cartCost}</Col>
              </Row>]
          }
          {isFormVisible && 
            <Row>
              <Col>
                <UserDataForm userData={userData} onSubmitForm={handleSendOrder} />
              </Col>
            </Row>}
        </Container>
      </Modal.Body>
      <Modal.Footer className="footer">
        <Button onClick={handleRejectBtnClick} variant="outline-danger">Відхилити</Button>
        {
          !isFormVisible && 
            <Button onClick={onApproveCartBtnClick} variant="outline-success">Підтвердити</Button>
        }
      </Modal.Footer>
    </Modal>
  );
};
