import React, { FC } from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import { TCategory, TProduct } from '../../helpers/types';
import { ProductCard } from '../ProductCard/ProductCard';
import './Category.scss';


export interface ICategory {
  category: TCategory;
  onBuyBtnClick: (category: TCategory, product: TProduct) => void;
}
export const Category: FC<ICategory> = ({ category, onBuyBtnClick }) => {
  return (
    <Container className="Category border-0">
      <Row className="row-cols-1 row-cols-md-2 row-cols-xl-3 g-4 row">
        {
          category?.items?.map(
            (product: TProduct) => 
              <Col key={`ctg${category.id}${product.id}`}>
                <ProductCard
                  product={product}
                  onBuyBtnClick={() => onBuyBtnClick(category, product)}
                />
              </Col>
          )
        }
      </Row>
    </Container>
  );
};

