import React, { FC } from 'react';
import { Button, Col, Container, Image, Modal, Row } from 'react-bootstrap';

import { TProduct } from '../../helpers/types';
import { getProductImgUrl } from '../../helpers/urls';
import { ProductCounter } from '../ProductCounter/ProductCounter';
import './PurchaseProductModal.scss';

export interface IPurchaseProductModal {
  isModalVisible: boolean;
  product: TProduct;
  productCount: number;
  onPurchaseBtnClick: () => void;
  onCloseModal: () => void;
  onProductCountChange: (count: number) => void;
}
export const PurchaseProductModal: FC<IPurchaseProductModal> = ({
  isModalVisible,
  product,
  productCount,
  onPurchaseBtnClick,
  onProductCountChange,
  onCloseModal,
}) => {
  const {
    currency,
    displayName,
    measure,
    measureUnit,
    pictureUrl,
    price,
    step,
  } = product;


  return (
    <Modal
      className="PurchaseProductModal"
      centered
      show={isModalVisible}
      size="xl"
      aria-labelledby="contained-modal-title-vcenter"
      onHide={onCloseModal}
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          {displayName}
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Container>
          <Row>
            <Col><Image rounded src={getProductImgUrl(pictureUrl)} /></Col>
            <Col>
              <h5 className="pb-3">Ціна: {price} {currency} за {measure}</h5>
              <h5 className="pb-3">Вартість: {productCount * price / step} {currency}</h5>
              <ProductCounter
                count={productCount}
                measureUnit={measureUnit}
                onIncreaseCount={() => onProductCountChange(productCount + step)}
                onDecreaseCount={() => onProductCountChange(productCount - step)}
              />
            </Col>
          </Row>
        </Container>
      </Modal.Body>
      <Modal.Footer className="footer">
        <Button onClick={onCloseModal} variant="outline-danger">Відхилити</Button>
        <Button onClick={onPurchaseBtnClick} variant="outline-success">Підтвердити</Button>
      </Modal.Footer>
    </Modal>
  );
};
