import React, { useMemo, useState } from "react";
import { find, isEmpty, reduce } from 'lodash';
import { CURRENCIES, products } from "../../helpers/constants";
import './Body.scss';
import { TCart, TCartItem, TCategory, TProduct, TUser } from "../../helpers/types";
import { Category, IMainNavbarItem, MainNavbar } from "../../components";
import { CartModal, PurchaseProductModal } from "../../components";
import { getProductImgUrl } from "../../helpers/urls";
import {
  changeArrayItemByIndex,
  deleteArrayItemByIndex,
  getUserDataFromStorage,
  saveUserDataToStorage,
  sendOrder,
} from "../../helpers/utils";

export const navItems: IMainNavbarItem[] = products.map((category: TCategory) => ({ id: category.id, displayName: category.displayName }));
export const userDataFromStorage: TUser = getUserDataFromStorage();

export const Body = () => {
  const [userData, setUserData] = useState<TUser>(userDataFromStorage);
  const [activeTabId, setActiveTabId] = useState<any>('0');
  const [cart, setCart] = useState<TCart>([]);
  const [isPurchaseProductModalVisible, setIsPurchaseProductModalVisible] = useState<boolean>(false);
  const [isCartModalVisible, setIsCartModalVisible] = useState<boolean>(false);
  const [productCount, setProductCount] = useState(0);
  const [selectedProduct, setSelectedProduct] = useState<TProduct>(products[0].items[0]);
  const [selectedCategory, setSelectedCategory] = useState<TCategory>(products[0]);

  const isCartEmpty: boolean = useMemo(() => isEmpty(cart), [cart]);

  const cartCost: string = useMemo(() => {
    const cost = reduce(cart, (acc: number, cartItem: TCartItem) => {
      return acc + cartItem.cost;
    }, 0);

    return `${cost} ${CURRENCIES.HRYVNIA}`;
  }, [cart]);
  
  const handleStartPurchaseProductBtnClick = (category: TCategory, product: TProduct) => {
    const cartItem: TCartItem | undefined = find(cart, {categoryId: category.id, productId: product.id});
    setSelectedProduct(product);
    setSelectedCategory(category);
    setProductCount(cartItem?.productCount || 0);
    setIsPurchaseProductModalVisible(true);
  }

  const addProductToCart = (category: TCategory, product: TProduct, productCount: number) => {
    const categoryId = category.id;
    const {
      id: productId,
      displayName,
      step,
      price,
      measure,
      measureUnit,
      currency,
      pictureUrl,
    } = product;
    const predicate =  {categoryId, productId};

    if (productCount === 0) {
      setCart(deleteArrayItemByIndex(cart, predicate));
      return;
    }

    const cost = productCount * price / step;
    const newCartItem: TCartItem = {
      categoryId,
      productId,
      displayName,
      pictureUrl: getProductImgUrl(pictureUrl),
      displayCount: `${productCount} ${measureUnit}`,
      productCount,
      cost,
      displayPriceShort: `${price}${currency}/${measure}`,
      displayCost: `${cost} ${currency}`,
    };

    setCart(changeArrayItemByIndex(cart, predicate, newCartItem));
  }

  const handleApplyPurchaseProductClick = () => {
    addProductToCart(selectedCategory, selectedProduct, productCount);
    handleClosePurchaseProductModal();
  }

  const handleClosePurchaseProductModal = () => {
    setIsPurchaseProductModalVisible(false);
    setProductCount(0);
  }

  const handlePurchaseCart = (newUserData: TUser) => {
    setUserData(newUserData);
    saveUserDataToStorage(newUserData);
    setIsCartModalVisible(false);
    sendOrder(newUserData, cart);
    setCart([]);
  }

  return (
    <div className="Body">
      <MainNavbar
        navItems={navItems}
        isCartEmpty={isCartEmpty}
        onNavSelect={setActiveTabId}
        onCartClick={() => setIsCartModalVisible(true)}
      />
      <Category
        category={products[activeTabId]}
        onBuyBtnClick={handleStartPurchaseProductBtnClick}
      />
      <PurchaseProductModal
        isModalVisible={isPurchaseProductModalVisible}
        productCount={productCount}
        product={selectedProduct}
        onProductCountChange={setProductCount}
        onPurchaseBtnClick={handleApplyPurchaseProductClick}
        onCloseModal={handleClosePurchaseProductModal}
      />
      <CartModal
        cart={cart}
        cartCost={cartCost}
        isCartEmpty={isCartEmpty}
        isCartModalVisible={isCartModalVisible}
        userData={userData}
        onCloseModal={() => setIsCartModalVisible(false)}
        onPurchaseBtnClick={handlePurchaseCart}
      />
    </div>
  );
};
