import { findIndex } from "lodash";
import { LOCAL_STORAGE_KEYS, targetEmail } from "./constants";
import { TCart, TCartItem, TUser } from "./types";

export type TChangeInArrayItemByIndex = (arr: any[], predicate: any, newItem: any) => any[];
export const changeArrayItemByIndex: TChangeInArrayItemByIndex = (arr, predicate, newItem) => {
  const index = findIndex(arr, predicate);

  if (index < 0) {
    return [...arr, newItem];
  }

  return [
    ...arr.slice(0, index),
    newItem,
    ...arr.slice(index + 1),
  ];
};


export type TDeleteInArrayItemByIndex = (arr: any[], predicate: any) => any[];
export const deleteArrayItemByIndex: TDeleteInArrayItemByIndex = (arr, predicate) => {
  const index = findIndex(arr, predicate);

  if (index < 0) {
    return arr;
  }

  return [
    ...arr.slice(0, index),
    ...arr.slice(index + 1),
  ];
};

export type TGetUserDataFromStorage = () => TUser;
export const getUserDataFromStorage: TGetUserDataFromStorage = () => {
  const restoredUserData: string | null = localStorage.getItem(LOCAL_STORAGE_KEYS.USER_DATA);
  if (!restoredUserData) {
    return {
      fullName: '',
      address: '',
      newPostNumber: null,
    }
  }

  return JSON.parse(restoredUserData);
}

export type TSaveUserDataToStorage = (userData: TUser) => void;
export const saveUserDataToStorage: TSaveUserDataToStorage = (userData) => {
  localStorage.setItem(LOCAL_STORAGE_KEYS.USER_DATA, JSON.stringify(userData));
}

